import { FC, useMemo, useState } from 'react';

import { IconButton } from '@mui/material';
import Button from 'buttons/Button';
import { useCurrencyContext } from 'contexts/CurrencyContext';
import { useLocaleContext } from 'contexts/LocaleContext';
import { IcoMoon } from 'custom/IcoMoon';
import { getCurrencyCodeWithSign } from 'helpers/getCurrencyCodeWithSign';
import { useTranslate } from 'hooks/index';
import { useGetWebsite } from 'hooks/queries';
import { LocalesAndCurrenciesModal } from 'modals/LocalesAndCurrenciesModal';

export type LocalesCurrencyButtonProps = {
  isOnlyIcon?: boolean;
};

export const LocalesCurrencyButton: FC<LocalesCurrencyButtonProps> = (props) => {
  const { isOnlyIcon } = props;

  const { currency } = useCurrencyContext();
  const { locale } = useLocaleContext();
  const translate = useTranslate({ basePath: 'modals.locales_and_currencies' });

  const { data: websiteData } = useGetWebsite();
  const { locales = [] } = websiteData || {};

  const localeRecord = useMemo(() => {
    return locales.find((record) => record.locale === locale);
  }, [locale, locales]);

  const [isLocalesAndCurrenciesModalOpen, setIsLocalesAndCurrenciesModalOpen] = useState(false);

  const handleCloseModalClick = () => {
    setIsLocalesAndCurrenciesModalOpen(false);
  };

  const handleOpenModalClick = () => {
    setIsLocalesAndCurrenciesModalOpen(true);
  };

  return (
    <>
      <LocalesAndCurrenciesModal
        open={isLocalesAndCurrenciesModalOpen}
        onClose={handleCloseModalClick}
      />
      {isOnlyIcon ? (
        <IconButton onClick={handleOpenModalClick} aria-label={translate('title')}>
          <IcoMoon icon="globe" />
        </IconButton>
      ) : (
        <Button
          startIcon={<IcoMoon icon="globe" color="inherit" />}
          color="secondary"
          onClick={handleOpenModalClick}
          aria-label={translate('title')}
        >
          {localeRecord?.title} -{' '}
          {getCurrencyCodeWithSign({
            currencyCode: currency?.code || '',
            currencySign: currency?.sign || '',
          })}
        </Button>
      )}
    </>
  );
};
