import { FC } from 'react';

import { Box, Container, Divider, Drawer, DrawerProps, Stack } from '@mui/material';
import { zodNavigatorSchemas } from '@packages/providers';
import { useQueryClient } from '@tanstack/react-query';
import { LocalesCurrencyButton } from 'buttons/LocalesCurrencyButton';
import { OwnerLoginButton } from 'buttons/OwnerLoginButton';
import Link from 'components/links/Link';
import { getNavigatorPathnames } from 'helpers/getNavigatorPathnames';
import { prefetchPage } from 'hooks/queries';
import { MOBILE_HEADER_HEIGHT } from 'theme/constants';

export interface MenuDrawerProps extends DrawerProps {
  onClose: () => void;
  headerItems: NonNullable<typeof zodNavigatorSchemas.show._output['items']>;
}

export const MenuDrawer: FC<MenuDrawerProps> = (props) => {
  const { onClose, headerItems, ...rest } = props;

  const queryClient = useQueryClient();

  return (
    <Drawer
      anchor="right"
      hideBackdrop
      PaperProps={{
        sx: {
          width: '100%',
          background: (theme) => theme.palette.background.default,
          border: 'none',
          paddingTop: `${MOBILE_HEADER_HEIGHT}px`,
          paddingBottom: 3,
        },
      }}
      {...rest}
    >
      <Container sx={{ height: '100%' }}>
        <Stack
          height="100%"
          px={{ xs: 0, sm: 2, md: 4 }}
          divider={<Divider flexItem />}
          spacing={4}
        >
          {/* We need `height: 1px` to make `overflow: auto` work.
              The actual height will take all the remaining space
              due to the `flex-grow: 1` */}
          <Stack height="1px" flexGrow={1} justifyContent="space-between" spacing={4}>
            <Stack pt={3} spacing={4} overflow="auto">
              {headerItems.map((item) => {
                const { title, target, path = '', id } = item;

                const { pathname, prefetchPath, isPrefetchPath } = getNavigatorPathnames(path);

                return (
                  <Link
                    key={id}
                    variant="body2"
                    t={title}
                    href={{ pathname }}
                    target={target}
                    color="text.primary"
                    sx={{
                      textDecoration: 'none',
                      wordBreak: 'break-word',
                      ':hover, :focus': { color: 'primary.main' },
                    }}
                    onClick={onClose}
                    onMouseEnter={() => {
                      if (isPrefetchPath) {
                        prefetchPage(queryClient, undefined, { path: prefetchPath });
                      }
                    }}
                    onTouchStart={() => {
                      if (isPrefetchPath) {
                        prefetchPage(queryClient, undefined, { path: prefetchPath });
                      }
                    }}
                  />
                );
              })}
            </Stack>
            <Box>
              <LocalesCurrencyButton />
            </Box>
          </Stack>
          <Box>
            <OwnerLoginButton />
          </Box>
        </Stack>
      </Container>
    </Drawer>
  );
};
