import { FC, SyntheticEvent, useCallback, useState } from 'react';

import {
  Box,
  dialogClasses,
  Grid,
  Stack,
  Tab,
  Tabs,
  Theme,
  Typography,
  useMediaQuery,
} from '@mui/material';
import { useCurrencyContext } from 'contexts/CurrencyContext';
import { useLocaleContext } from 'contexts/LocaleContext';
import { CustomButton } from 'custom/CustomButton';
import { IcoMoon } from 'custom/IcoMoon';
import { getCurrencyCodeWithSign } from 'helpers/getCurrencyCodeWithSign';
import { useTranslate } from 'hooks/index';
import { useGetCurrencies } from 'hooks/queries/useGetCurrencies';
import { useGetLocales } from 'hooks/queries/useGetLocales';
import { partition } from 'lodash';
import { ModalBase, ModalBaseProps } from 'modals/ModalBase';
import { styled } from 'theme/index';
import { LocalesRecord } from 'types/index';

const mainCurrenciesCodes = ['USD', 'EUR'];

const a11yTabPanelProps = (index: number) => {
  return {
    id: `locales-currencies-tabpanel-${index}`,
    'aria-labelledby': `locales-currencies-tab-${index}`,
  };
};

export type TabPanelProps = {
  index: number;
  value: number;
  onModalClose?: ModalBaseProps['onClose'];
};

// ----------------------------------------------
//                    Locales Tab
// ----------------------------------------------
const LocalesTabPanel: FC<TabPanelProps> = (props) => {
  const { value, index, onModalClose, ...rest } = props;
  const { data: locales = [] } = useGetLocales();

  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const { locale: selectedLocale, setLocale: setSelectedLocale } = useLocaleContext();

  const handleLocaleClick = (selectedLocale: LocalesRecord) => {
    const { locale } = selectedLocale;
    setSelectedLocale(locale);
    onModalClose?.({}, 'backdropClick');
  };

  return (
    <Box role="tabpanel" hidden={value !== index} {...a11yTabPanelProps(index)} {...rest}>
      {value === index && (
        <Grid container spacing={isMobile ? 2 : 3}>
          {locales.map((localeRecord, index) => {
            const { country, locale, native } = localeRecord;

            return (
              <Grid item key={index} xs={6} sm={6} md={4} lg={3} xl={3}>
                <CustomButton
                  title={native}
                  subtitle={country?.title}
                  onClick={() => handleLocaleClick(localeRecord)}
                  isSelected={selectedLocale === locale}
                />
              </Grid>
            );
          })}
        </Grid>
      )}
    </Box>
  );
};

// ----------------------------------------------
//                    Currencies Tab
// ----------------------------------------------
const CurrenciesTabPanel: FC<TabPanelProps> = (props) => {
  const { value, index, onModalClose, ...rest } = props;
  const { currency: currentCurrency, setCurrency: setCurrentCurrency } = useCurrencyContext();
  const { data: currencies = [] } = useGetCurrencies();

  const [mainCurrencies, additionalCurrencies] = partition(currencies, (currency) =>
    mainCurrenciesCodes.includes(currency.code),
  );

  const sortedCurrencies = additionalCurrencies.sort((a, b) => a.title.localeCompare(b.title));

  const finalCurrencies = [...mainCurrencies, ...sortedCurrencies];

  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const handleCurrencySelect = useCallback(
    (code: string) => {
      setCurrentCurrency(code);
      onModalClose?.({}, 'backdropClick');
    },
    [onModalClose, setCurrentCurrency],
  );

  return (
    <Box role="tabpanel" hidden={value !== index} {...a11yTabPanelProps(index)} {...rest}>
      {value === index && (
        <Grid container spacing={isMobile ? 2 : 3}>
          {finalCurrencies.map((currency, index) => {
            const { title, code, sign } = currency;

            return (
              <Grid item key={index} xs={6} sm={6} md={4} lg={3} xl={3}>
                <CustomButton
                  title={title}
                  subtitle={getCurrencyCodeWithSign({
                    currencyCode: code,
                    currencySign: sign,
                  })}
                  onClick={() => handleCurrencySelect(code)}
                  isSelected={currentCurrency?.code === code}
                />
              </Grid>
            );
          })}
        </Grid>
      )}
    </Box>
  );
};

const PREFIX = 'LocalesAndCurrenciesModal';

const StyledLocalesAndCurrenciesModal = styled(ModalBase, {
  name: `Styled--${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
})(() => {
  return {
    [`&& .${dialogClasses.paper}`]: {
      maxWidth: 'none',
      width: 1196,
    },
  };
});

export type LocalesAndCurrenciesModalProps = ModalBaseProps;

const a11yProps = (index: number) => {
  return {
    id: `locales-currencies-tab-${index}`,
    'aria-controls': `locales-currencies-${index}`,
  };
};

export const LocalesAndCurrenciesModal: FC<LocalesAndCurrenciesModalProps> = (props) => {
  const { onClose } = props;
  const [activeTab, setActiveTab] = useState(0);
  const translate = useTranslate({ basePath: 'modals.locales_and_currencies' });
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));

  const handleChangeTab = (_: SyntheticEvent, newValue: number) => {
    setActiveTab(newValue);
  };

  return (
    <StyledLocalesAndCurrenciesModal
      title={translate('title')}
      subtitle={translate('subtitle')}
      fullScreen={isMobile}
      {...props}
    >
      <Tabs
        value={activeTab}
        onChange={handleChangeTab}
        aria-label="locales and currencies"
        sx={{ mb: 2 }}
      >
        <Tab
          label={
            <Stack direction="row" spacing={1} alignItems="center">
              <IcoMoon icon="globe" color="inherit" />
              <Typography>{translate('tab_labels.first_tab')}</Typography>
            </Stack>
          }
          {...a11yProps(0)}
        />
        <Tab label={translate('tab_labels.second_tab')} {...a11yProps(1)} />
      </Tabs>

      <LocalesTabPanel value={activeTab} index={0} onModalClose={onClose} />
      <CurrenciesTabPanel value={activeTab} index={1} onModalClose={onClose} />
    </StyledLocalesAndCurrenciesModal>
  );
};
