import { FC, MouseEvent, useCallback, useMemo, useState } from 'react';

import { useBuilderState } from '@king-david-it/builder';
import {
  AppBar,
  Box,
  Container,
  IconButton,
  Menu,
  MenuItem,
  Theme,
  Toolbar,
  useMediaQuery,
} from '@mui/material';
import { NextImage } from '@packages/components';
import { useBoolean } from '@packages/helpers';
import { zodNavigatorSchemas } from '@packages/providers';
import { StyledRootProps } from '@packages/types';
import { useQueryClient } from '@tanstack/react-query';
import { LocalesCurrencyButton } from 'buttons/LocalesCurrencyButton';
import { OwnerLoginButton } from 'buttons/OwnerLoginButton';
import { MenuDrawer } from 'components/drawers/MenuDrawer';
import { Typography } from 'components/Typography';
import { IcoMoon } from 'custom/IcoMoon';
import { getNavigatorItems } from 'helpers/getNavigatorItems';
import { getNavigatorPathnames } from 'helpers/getNavigatorPathnames';
import { prefetchPage, useGetWebsite, useTranslate } from 'hooks/index';
import get from 'lodash/get';
import { HEADER_HEIGHT, MOBILE_HEADER_HEIGHT, theme } from 'theme/index';

import { useResponsiveHeaderItems } from './useResponsiveHeaderItems';

import { Link } from '../../links/Link';

export type HeaderProps = StyledRootProps;

const MenuToolbar = () => {
  const [overflowingItemsMenuAnchor, setOverflowingItemsMenuAnchor] = useState<HTMLElement | null>(
    null,
  );
  const isMobile = useMediaQuery<Theme>((theme) => theme.breakpoints.down('md'));
  const translate = useTranslate({ basePath: 'menu.app_bar' });
  const { data: websiteData } = useGetWebsite();

  const {
    value: isMobileDrawerOpen,
    setFalse: closeMobileDrawer,
    toggle: toggleMobileDrawer,
  } = useBoolean(false);

  const queryClient = useQueryClient();

  const headerHeight = isMobile ? MOBILE_HEADER_HEIGHT : HEADER_HEIGHT;
  const navigators = useMemo(() => get(websiteData, 'navigators') || [], [websiteData]);
  const logo = get(websiteData, 'logo_landscape');
  const logoName = get(logo, 'name');
  const logoSrc = get(logo, 'src');

  const headerItems = useMemo(
    () =>
      getNavigatorItems({
        navigatorsData: zodNavigatorSchemas.index.parse(navigators),
        selector: 'header',
      }),
    [navigators],
  );

  const [headerItemsContainer, setHeaderItemsContainer] = useState<HTMLDivElement | null>(null);

  const [visibleItems, menuItems] = useResponsiveHeaderItems({
    container: headerItemsContainer,
    items: headerItems,
  });

  const areVisibleItemsExist = Boolean(visibleItems.length);
  const areMenuItemsExist = Boolean(menuItems.length);
  const shouldShowMenuIcon = areVisibleItemsExist && areMenuItemsExist;
  const isOverflowingItemsMenuOpen = Boolean(overflowingItemsMenuAnchor) && areMenuItemsExist;

  const handleOverflowingItemsButtonClick = useCallback((e: MouseEvent<HTMLElement>) => {
    setOverflowingItemsMenuAnchor(e.currentTarget);
  }, []);

  const handleOverflowingItemsMenuClose = useCallback(() => {
    setOverflowingItemsMenuAnchor(null);
  }, []);

  return (
    <Container maxWidth="xl">
      <MenuDrawer open={isMobileDrawerOpen} onClose={closeMobileDrawer} headerItems={headerItems} />
      <Menu
        open={isOverflowingItemsMenuOpen}
        anchorEl={overflowingItemsMenuAnchor}
        onClose={handleOverflowingItemsMenuClose}
      >
        {menuItems.map((item) => {
          const { title, target, path = '', id } = item;

          const { pathname, prefetchPath, isPrefetchPath } = getNavigatorPathnames(path);

          return (
            <MenuItem
              key={id}
              component="a"
              LinkComponent={Link}
              href={pathname}
              target={target}
              sx={{
                ':hover, :focus': { color: 'primary.main' },
              }}
              onMouseEnter={() => {
                if (isPrefetchPath) {
                  prefetchPage(queryClient, undefined, { path: prefetchPath });
                }
              }}
            >
              <Typography variant="body2" overflow="hidden" textOverflow="ellipsis">
                {title}
              </Typography>
            </MenuItem>
          );
        })}
      </Menu>
      <Toolbar disableGutters>
        <Box
          width="100%"
          display="flex"
          justifyContent="space-between"
          alignItems="center"
          flexWrap="nowrap"
        >
          <Box flexShrink={0}>
            <Link href="/" aria-label={translate('home')} sx={{ display: 'flex' }}>
              <NextImage
                src={logoSrc || '/icons/MaveriksLogo.svg'}
                width={124}
                height={headerHeight}
                alt={logoName || 'Logo'}
                priority
                style={{ objectFit: 'contain' }}
              />
            </Link>
          </Box>
          <Box
            sx={{ display: { xs: 'none', md: 'flex' } }}
            justifyContent="space-between"
            alignItems="center"
            ml={9}
            mr="auto"
            overflow="hidden"
          >
            <Box
              ref={setHeaderItemsContainer}
              display="flex"
              flexGrow={1}
              gap={5}
              mr={3}
              overflow="hidden"
            >
              {visibleItems.map((item) => {
                const { title, target, path = '', id } = item;

                const { pathname, prefetchPath, isPrefetchPath } = getNavigatorPathnames(path);

                return (
                  <Box
                    key={id}
                    flexShrink={0}
                    maxWidth={191}
                    overflow="hidden"
                    textOverflow="ellipsis"
                  >
                    <Link
                      variant="body2"
                      t={title}
                      href={{
                        pathname,
                      }}
                      target={target}
                      color="text.primary"
                      sx={{
                        textDecoration: 'none',
                        whiteSpace: 'nowrap',
                        ':hover, :focus': { color: 'primary.main' },
                      }}
                      onMouseEnter={() => {
                        if (isPrefetchPath) {
                          prefetchPage(queryClient, undefined, { path: prefetchPath });
                        }
                      }}
                    />
                  </Box>
                );
              })}
            </Box>
            {shouldShowMenuIcon && (
              <IconButton onClick={handleOverflowingItemsButtonClick}>
                <IcoMoon icon="dots" />
              </IconButton>
            )}
          </Box>
          <Box
            display="flex"
            gap={3}
            justifyContent="flex-end"
            alignItems="center"
            flexWrap="nowrap"
            flexShrink={0}
            marginLeft={3}
          >
            <Box sx={{ display: { md: 'none' } }}>
              <Box>{!isMobileDrawerOpen && <LocalesCurrencyButton isOnlyIcon />}</Box>
            </Box>
            <Box sx={{ display: { xs: 'none', md: 'block' } }}>
              <Box>{!isMobileDrawerOpen && <LocalesCurrencyButton />}</Box>
            </Box>

            <Box sx={{ display: { xs: 'none', md: 'block' } }}>
              <OwnerLoginButton />
            </Box>

            <Box sx={{ display: { md: 'none' } }}>
              <IconButton onClick={toggleMobileDrawer}>
                {isMobileDrawerOpen ? <IcoMoon icon="close" /> : <IcoMoon icon="menu" />}
              </IconButton>
            </Box>
          </Box>
        </Box>
      </Toolbar>
    </Container>
  );
};

const Header: FC<HeaderProps> = (props) => {
  const { sx } = props;

  const { isEditing } = useBuilderState();

  return (
    <>
      {!isEditing && (
        // default app bar to hold space
        <AppBar
          component="div"
          position="static"
          color="transparent"
          sx={{
            border: 'none',
            boxShadow: 'none',
            height: { xs: MOBILE_HEADER_HEIGHT, md: HEADER_HEIGHT },
          }}
        />
      )}
      <AppBar
        position={isEditing ? 'static' : 'fixed'}
        color="transparent"
        sx={{
          bgcolor: 'background.default',
          color: 'text.primary',
          zIndex: 'modal',
          boxShadow: 'none',
          height: { xs: MOBILE_HEADER_HEIGHT, md: HEADER_HEIGHT },
          borderBottom: `1px solid ${theme.palette.primary.background}`,
          ...sx,
        }}
      >
        <MenuToolbar />
      </AppBar>
    </>
  );
};

export default Header;
