import { FC, ReactElement } from 'react';

import {
  Box,
  Button,
  ButtonProps,
  generateUtilityClasses,
  Typography,
  useTheme,
} from '@mui/material';
import { IcoMoon } from 'custom/IcoMoon';
import { styled } from 'theme/index';

const PREFIX = 'CustomButton';

export const CustomButtonClasses = generateUtilityClasses(PREFIX, [
  'iconWrapper',
  'checkIconRoot',
  'contentWrapper',
]);

const StyledCustomButton = styled(Button, {
  name: `Styled--${PREFIX}`,
  label: `Sapiens--${PREFIX}`,
  shouldForwardProp: (p) => {
    return p !== 'isSelected';
  },
})<{ isSelected: boolean }>(({ theme, isSelected }) => {
  return {
    position: 'relative',
    width: '100%',
    height: 64,
    borderRadius: 12,
    display: 'flex',
    justifyContent: 'start',
    alignItems: 'center',
    border: `1px solid ${isSelected ? theme.palette.primary.main : theme.palette.primary.border}`,
    background: theme.palette.background.default,
    padding: theme.spacing(1.5, 2),
    textDecoration: 'none',
    textAlign: 'start',

    '&:hover': {
      textDecoration: 'none',
      background: 'inherit',
      ':not(:disabled)': {
        cursor: 'pointer',
        borderColor: theme.palette.primary.main,
        transition: theme.transitions.create('border-color', {
          duration: theme.transitions.duration.standard,
        }),
      },
    },
    [`& .${CustomButtonClasses.iconWrapper}`]: {
      display: 'flex',
      marginRight: theme.spacing(2),
    },
    [`& .${CustomButtonClasses.contentWrapper}`]: {
      textAlign: 'start',
    },
    [`& .${CustomButtonClasses.checkIconRoot}`]: {
      position: 'absolute',
      right: 16,
    },
  };
});

export type CustomButtonProps = Partial<ButtonProps> & {
  title: string;
  subtitle?: string;
  isSelected?: boolean;
  icon?: ReactElement;
};

export const CustomButton: FC<CustomButtonProps> = (props) => {
  const { title, subtitle, isSelected = false, icon, ...rest } = props;
  const theme = useTheme();

  return (
    <StyledCustomButton isSelected={isSelected} {...rest}>
      {isSelected && (
        <IcoMoon
          icon="check"
          color={theme.palette.primary.main}
          className={CustomButtonClasses.checkIconRoot}
        />
      )}
      {icon && <Box className={CustomButtonClasses.iconWrapper}>{icon}</Box>}
      <Box className={CustomButtonClasses.contentWrapper}>
        <Typography variant="subtitle2">{title}</Typography>
        <Typography variant="subtitle1" color="text.light">
          {subtitle}
        </Typography>
      </Box>
    </StyledCustomButton>
  );
};
